.profile {
  display: flex;
}

.avatarBlock {
  display: flex;
  flex-direction: column;
  align-items: end;
}

.mainPhoto {
  object-fit: cover;
  width: 100%;
  min-width: 100px;
  max-width: 300px;
}

.previewImage img {
  width: 500px;
  margin: -250px auto;
}

.fileInput {
  display: none;
}

.customFileUpload {
  width: 30px;
  height: 30px;
  border: 1px solid #ccc;
  padding: 6px 12px;
  cursor: pointer;
  background-color: #f2f2f2;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  opacity: 40%;
}

.customFileUpload:hover {
  background-color: #e2e2e2;
  opacity: 1;
}

.profileContent {
  flex-grow: 1;
}
